import React from "react";
import { useMoralis, useNFTBalances } from "react-moralis";
import { Card, Image, Tooltip, Skeleton } from "antd";
import { FileSearchOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { getExplorer } from "helpers/networks";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
import { getEllipsisTxt } from "../helpers/formatters";
import { mainBackgroundCol, brightFontCol } from "GlobalStyles";

const { Meta } = Card;

const styles = {
  NFTs: {
    display: "flex",
    flexWrap: "wrap",
    WebkitBoxPack: "start",
    justifyContent: "flex-start",
    margin: "0 auto",
    maxWidth: "1000px",
    width: "100%",
    gap: "10px",
  },
};

function NFTBalance() {
  const { data: NFTBalances } = useNFTBalances();
  const { chainId } = useMoralis();
  const { verifyMetadata } = useVerifyMetadata();

  console.log("NFTBalances", NFTBalances);
  return (
    <div style={{
      padding: "15px",
      maxWidth: "1030px",
      width: "100%",
      background: mainBackgroundCol,
      color: brightFontCol,
    }}>
      <h1> NFT Balances</h1>
      <br />
      <div style={styles.NFTs}>
        <Skeleton loading={!NFTBalances?.result}>
          {NFTBalances?.result &&
            NFTBalances.result
              .filter(nft => nft.image)
              .map((nft, index) => {
                //Verify Metadata
                nft = verifyMetadata(nft);
                return (
                  <Card
                    hoverable
                    actions={[
                      <Tooltip title="View On Blockexplorer">
                        <FileSearchOutlined
                          onClick={() => window.open(`${getExplorer(chainId)}address/${nft.token_address}`, "_blank")}
                        />
                      </Tooltip>,
                      <Tooltip title="Sell On OpenSea">
                        <ShoppingCartOutlined onClick={() => alert("OPENSEA INTEGRATION COMING!")} />
                      </Tooltip>,
                    ]}
                    style={{ width: 240, border: "2px solid #e7eaf3" }}
                    cover={
                      <Image
                        preview={false}
                        src={nft?.image || "error"}
                        fallback="data:image/png;base64,"
                        alt=""
                        style={{ height: "300px" }}
                      />
                    }
                    key={index}
                  >
                    <Meta title={nft.name} description={
                      <>
                        <p>tokenAddress:</p>
                        <p>{getEllipsisTxt(nft.token_address, 11)}</p>
                        <p>tokenId: {nft.token_id}</p>
                      </>
                    } />
                  </Card>
                );
              })}
        </Skeleton>
      </div>
    </div>
  );
}

export default NFTBalance;
